.main-nav {
  background-color: #81b3d2;
  width: 320px;
  margin: 0 auto;
  position: relative;
  flex: 0 0 100%;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
    margin: 0;
    background-color: #ffffff;
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.85714286;
    text-transform: uppercase;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 1200px;
  }
}
.main-nav--oppened .main-nav__list {
  display: block;
}
.main-nav__toggle--hidden {
  display: block;
}
.main-nav__toggle {
  position: absolute;
  z-index: 2;
  display: block;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
  bottom: -43px;
  right: 17px;
  width: 32px;
  height: 32px;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 4px;
  background: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 17px 0 0 #81b3d2;
  top: 6px;
  right: 4px;
}
.main-nav__toggle:hover::before {
  background-color: #b3d1e4;
  box-shadow: 0 8px 0 0 #b3d1e4, 0 16px 0 0 #b3d1e4;
}
.main-nav__toggle:active::before {
  background-color: #d9e8f1;
  box-shadow: 0 8px 0 0 #d9e8f1, 0 16px 0 0 #d9e8f1;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--oppened {
  top: 30px;
  right: 20px;
  left: auto;
  background-color: #81b3d2;
}
.main-nav__toggle--oppened::before,
.main-nav__toggle--oppened::after {
  content: "";
  position: absolute;
  top: 13px;
  right: 5px;
  box-shadow: none;
  width: 23px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle--oppened::before {
  transform: rotate(45deg);
}
.main-nav__toggle--oppened::after {
  transform: rotate(-45deg);
}
.main-nav__toggle--oppened:hover::before,
.main-nav__toggle--oppened:hover::after {
  background-color: #cde1ed;
  box-shadow: none;
}
.main-nav__toggle--oppened:active::before,
.main-nav__toggle--oppened:active::after {
  background-color: #a7cae0;
  box-shadow: none;
}
.main-nav__burger {
  font-size: 0;
  line-height: 0;
}
